import { useEffect } from "react";

/**
 * Core hook to attach listeners to the document.
 */
export const useWindowEvent = (
  event: string, 
  callback: (ev: any) => void
): void => {
  useEffect(() => {
    document.addEventListener(event, callback);
    return () => document.removeEventListener(event, callback);
  }, [event, callback]);
};

// --- Video Events ---

export const useGlobalPauseEvent = (callback: (e: CustomEvent) => void) => {
  return useWindowEvent("video:toggle", callback);
};

// --- Mute Events ---

export const useGlobalMuteToggleEvent = (callback: (e: CustomEvent) => void) => {
  return useWindowEvent("mute:toggle", callback);
};

export const useGlobalMuteEvent = (callback: (e: CustomEvent) => void) => {
  return useWindowEvent("mute", callback);
};

export const useGlobalUnmuteEvent = (callback: (e: CustomEvent) => void) => {
  return useWindowEvent("unmute", callback);
};

// ---  Dispatchers ---

export const dispatchGlobalPause = (payload?: any) => {
  const event = new CustomEvent("video:toggle", { detail: payload });
  document.dispatchEvent(event);
};

export const dispatchMuteToggle = () => {
  document.dispatchEvent(new CustomEvent("mute:toggle"));
};

export const dispatchMute = () => {
  document.dispatchEvent(new CustomEvent("mute"));
};

export const dispatchUnmute = () => {
  document.dispatchEvent(new CustomEvent("unmute"));
};